import React, { useState } from "react";
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import PhoneIcon from '@mui/icons-material/Phone';
import MenuIcon from '@mui/icons-material/Menu';
import './Navigation.css';
import Profile from "../Profile";
import AboutMe from "../AboutMe";
import Projects from "../Projects";
import Books from "../Books";
import ContactMe from "../contactMe";
import DarkModeSwitch from "../Dark-Mode-Switch/darkMode";
import Hidden from '@material-ui/core/Hidden';

function Panel(props) {
    const { children, value, index } = props;

    return (
        <div>
            {value === index && (
                <Box p={3} style={{ alignContent: 'center', margin: 'auto' }}>
                    {children}
                </Box>
            )}
        </div>
    );
}

const Navigation = () => {
    const [value, setValue] = React.useState(0);
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    
        handleMenuClose();
    };

    const handleMenuClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleMenuClose = () => {
        setAnchorEl(null);
    };

    return (
        <div>
            <header className="header">
                <div className="container">
                    <div className="row align-items-center">
                        <Profile />

                        <div className="col-12 col-md-auto" style={{ marginTop: "-20px" }}>
                            <Hidden mdUp>
                                <IconButton
                                    edge="end"
                                    color="inherit"
                                    aria-label="menu"
                                    onClick={handleMenuClick}
                                >
                                    <MenuIcon />
                                </IconButton>
                            </Hidden>

                            <Hidden smDown>
                                <Tabs
                                    value={value}
                                    onChange={handleChange}
                                    aria-label="icon position tabs example"
                                    TabIndicatorProps={{ className: "hidden sm:block" }}
                                    classes={{
                                        flexContainer: "flex-wrap sm:flex-nowrap",
                                    }}
                                >
                                    <Tab label="About me" />
                                    <Tab label="Projects" />
                                    <Tab label="Books" />
                                    <Tab label={
                                            <span style={{ display: "flex", alignItems: "center" }}>
                                                <PhoneIcon style={{ marginRight: "7px" }} />
                                                Contact me
                                            </span>
                                               }
                  />
                                </Tabs>
                            </Hidden>
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                            <DarkModeSwitch />
                        </div>
                    </div>
                </div>
            </header>

            <Panel value={value} index={0}>
                <AboutMe />
            </Panel>

            <Panel value={value} index={1}>
                <Projects />
            </Panel>

            <Panel value={value} index={2}>
                <Books />
            </Panel>
            <Panel value={value} index={3}>
                <ContactMe />
            </Panel>

            <Menu
  anchorEl={anchorEl}
  open={Boolean(anchorEl)}
  onClose={handleMenuClose}
>
  <MenuItem
    selected={value === 0}
    onClick={() => {
      handleChange(null, 0);
      handleMenuClose(); // Close the menu
    }}
  >
    About me
  </MenuItem>
  <MenuItem
    selected={value === 1}
    onClick={() => {
      handleChange(null, 1);
      handleMenuClose(); // Close the menu
    }}
  >
    Projects
  </MenuItem>
  <MenuItem
    selected={value === 2}
    onClick={() => {
      handleChange(null, 2);
      handleMenuClose(); // Close the menu
    }}
  >
    Books
  </MenuItem>
  <MenuItem
    selected={value === 3}
    onClick={() => {
      handleChange(null, 3);
      handleMenuClose(); // Close the menu
    }}
  >
    Contact Me
  </MenuItem>
</Menu>

</div>
    );
}

export default Navigation;
